<template>
  <div class="layout-content" >
    <mt-header title="办结登记" class="topNav">
      <router-link :to="{name: 'main'}" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
    </mt-header>

    <div class="layout-content-main">
      <Form style="width: 100%;overflow-x: hidden;">
        <Form-item label="">
           处理结果描述
          <Input  v-model="formParams.presult" type="textarea" :autosize="{minRows: 2,maxRows: 5}" placeholder="请输入..."></Input>
        </Form-item>
        <Form-item label="">
          <Row>
            <Col span="4">事件等级</Col>
            <Col span="20" style="text-align: left">
            <Select v-model="formParams.grade" placeholder="请选择">
              <Option value="0">预警级</Option>
              <Option value="1">一级</Option>
              <Option value="2">二级</Option>
              <Option value="3">三级</Option>
              <Option value="4">四级</Option>
            </Select>
            </Col>
          </Row>
        </Form-item>
        <Form-item label="" >
          <Row>
            <Col span="4">附件</Col>
            <Col span="20" style="text-align: left"> <i class="icon icon-form-tj" @click="loadImageUpload"></i> </Col>
          </Row>
        </Form-item>
        <Form-item>
          <Row :gutter="16">
            <Col span="24">
            <img v-for="url in imgUrlList" :src="url" style="width: 70px; height: 70px">
            </Col>
          </Row>
        </Form-item>
      </Form>
    </div>

    <div data-am-widget="navbar" class="am-navbar am-cf am-navbar-default am-no-layout" id="">
      <ul class="am-navbar-nav am-cf am-avg-sm-4">
        <li @click="dealEvent">
          <span class="am-navbar-label">提交</span>
        </li>
      </ul>
      <ul class="am-navbar-actions"></ul>
    </div>
  </div>
</template>

<script>

  import Qs from 'qs'
  import Utils from '../assets/js/utils'
  import Vue from 'vue'
  import { Header } from 'mint-ui';

  Vue.component(Header.name, Header);
  export default {
    name: 'DoneRegistration',
    data(){
      return{
        options: null,
        errorMsg: null,
        inspection: null,
        uploadList: [],
        imgUrlList: [],
        formParams: {
          id: null,
          roomId: null,
          handler: null,
          presult: null,
          grade: null,
          ppicture: null
        }
      }
    },
    created(){
      this.formParams.id = this.$route.params.id;
      this.getEventDetail();
    },
    mounted(){
      this.options = this.setOptions( Camera.PictureSourceType.CAMERA);
    },
    methods:{
        getEventDetail(){
          $.ajax({
            type : "get",
            url : this.$api+'/inspectevent/detail',
            data: {
              inspectid: this.$route.params.id,
              token:1
            },
            dataType : "json",
            success : function(data) {
              if(data.success == true){
                this.inspection = data.obj.inspection;
                this.formParams.roomId = this.inspection.roomid;
              }else{
                this.$Message.success('查询失败');
              }
            }.bind(this)
          });
        },
      dealEvent(){
        var token = Utils.getCookie('token');
        if(token == null){
          this.$Message.error("用户未登陆");
          this.$router.push({name: 'login'});
        }
        this.formParams.handler = token.split('|')[0];
        this.formParams.token=token;
        $.ajax({
          type : "post",
          url : this.$api+'/inspectevent/deal',
          data: Qs.stringify(this.formParams),
          dataType : "json",
          success : function(data) {
            if(data.success == true){
              this.$Message.success("事件登记成功");
              this.$router.push({name: 'list'});
            }else{
              this.$Message.error(data.msg);
            }
          }.bind(this)
        });
      },
      goBack(){
        this.$router.go(-1);
      },
      setOptions(srcType) {
        var options = {
          // Some common settings are 20, 50, and 100
          quality: 50,
          // destinationType: Camera.DestinationType.FILE_URI,
          destinationType: Camera.DestinationType.DATA_URL,
          // In this app, dynamically set the picture source, Camera or photo gallery
          sourceType: srcType,
          encodingType: Camera.EncodingType.JPEG,
          mediaType: Camera.MediaType.PICTURE,
          allowEdit: true,
          correctOrientation: true  //Corrects Android orientation quirks
        }
        return options;
      },
      openCamera() {
        navigator.camera.getPicture(this.onLoadImageSuccess, this.onLoadImageFail, {destinationType: Camera.DestinationType.DATA_URL});
      },
      //拍照上传
      loadImageUpload() {
        var _this = this;
        api.getPicture({
          sourceType: 'camera',
          encodingType: 'jpg',
          mediaValue: 'pic',
          destinationType: 'url',
          allowEdit: true,
          quality: 50,
          saveToPhotoAlbum: false
        }, function (ret, err) {
          var cookie = Utils.getCookie('token');
          var token = cookie.split('|')[0];
          var imgPath = ret.data;
          api.showProgress({
            title: '正在上传...',
            modal: false
          });
          api.ajax({
            url: _this.$api + '/image/upload?token=' + token,
            method: 'post',
            data: {
              files: {
                file: imgPath
              }
            }
          }, function (ret, err) {
            api.hideProgress();
            if (ret) {
              var url = ret.obj.url;
              _this.imgUrlList.push(_this.$apit + "/images" + url);
              _this.url_arr.push(url)
              _this.formParams.ipicture = _this.url_arr.join();
              console.log(_this.$apit + "/images" + url)
            } else {
              alert(JSON.stringify(err));
            }
          });

        })

        // //拍照上传并显示在屏幕
        // navigator.camera.getPicture(this.onLoadImageUploadSuccess, this.onLoadImageFail, {
        //   destinationType: Camera.DestinationType.FILE_URI
        // });

      },
      //图片拍照成功后回调此函数
      onLoadImageUploadSuccess(imageURI) {
        var _this  = this;
        var cookie = Utils.getCookie('token');
        var token=cookie.split('|')[0];
        //此处执行文件上传的操作，上传成功后执行下面代码
        var options = new FileUploadOptions(); //文件参数选项
        options.fileKey = "file";//向服务端传递的file参数的parameter name
        options.fileName = imageURI.substr(imageURI.lastIndexOf('/') + 1);//文件名
        options.mimeType = "image/jpeg";//文件格式，默认为image/jpeg
        options.chunkedMode = false;

        var params = {};
        params.token = token;
        options.params = params;
        var ft = new FileTransfer();//文件上传类
        ft.onprogress = function (progressEvt) {//显示上传进度条
          if (progressEvt.lengthComputable) {
            navigator.notification.progressValue(Math.round(( progressEvt.loaded / progressEvt.total ) * 100));
          }
        }

        navigator.notification.progressStart("提醒", "当前上传进度");
        ft.upload(imageURI, encodeURI(this.$api+'/image/upload'), function (data) {
          navigator.notification.progressStop();//停止进度条
          _this.imgUrlList.push(imageURI);
          data.response= eval('(' + data.response + ')');
          _this.formParams.ppicture = data.response.obj.url;
          navigator.notification.alert("文件上传成功！", null, "提醒");
        }, _this.uploadFail, options, true);
      },
      uploadFail(error){
        navigator.notification.alert("拍照失败，原因："+ error.code + "——" + message, null, "警告");
      },
      onLoadImageSuccess(imageURI) {
        //这里的图片经过了base64编码
        var src = "data:image/jpeg;base64," + imageURI;
        // $("#getImage").attr("src", src);
        // $("#getImage").show();
      },
      //所有获取图片失败都回调此函数
      onLoadImageFail(message) {
        navigator.notification.alert("拍照失败，原因：" + message, null, "警告");
      }
    }
  }
</script>



